export default{
  data() {
    return {
      min: 0,
      max: 100,
      step: 0.1,
      playing: false,
    };
  },
  methods: {
    onInput(e) {
      const v = (e.target.value * 100) / this.max;
      const range = this.$refs.range;
      range.style.backgroundSize = `100% 3px, ${v}% 3px`;
    },
    play() {
      this.playing = !this.playing;
    },
  },
};



